<template>
  <div class="div1">
     <span id="content" con = "12345"><span id="spanContent">12345</span></span>
  </div>

</template>

<script>
export default {
  name: 'HelloWorld',
  data () {
    return {
      msg: 'Welcome to Your Vue.js App'
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.div1{
  font-size:24px;
  width:300px;
  height:200px;
  border:1px solid red;
  display:flex;
  justify-content:center;
  align-items:center;
  background:#99C3E5;
  overflow: hidden;
  /*position:absolute;*/
}
#spanContent{
  opacity:0;
  display:block;
  transform: scale(0);
  transition: opacity,transform 3s;

}
.div1:hover #spanContent{
  opacity:1;
  transform: scale(1);
  transition: opacity,transform 3s;
}
.div1:hover #content:after{
  content:attr(con);
  display:flex;
  align-items:flex-end;
  /*justify-content: center;*/
  overflow: hidden;
  position:absolute;
  width:100%;
  height:50%;
  left:0;
  bottom:-20px;
  transition:bottom 2s;
}
.div1:hover #content:before{
  content:attr(con);
  display:flex;
  align-items:flex-start;
  overflow: hidden;
  position:absolute;
  width:100%;
  height:50%;
  left:0;
  top:-20px;
  transition:top 2s;
}
.div1>#content{
  position:absolute;
  overflow: hidden;
}
#content:before{
   content:attr(con);
   display:flex;
   align-items:flex-start;
   overflow: hidden;
   position:absolute;
  width:100%;
  height:50%;
  left:0;
  top:0;
  transition:top 2s;
  /*top:16px;*/
 }
#content:after{
  content:attr(con);
  display:flex;
  align-items:flex-end;
  /*justify-content: center;*/
  overflow: hidden;
  position:absolute;
  width:100%;
  height:50%;
  left:0;
  bottom:0;
  transition:bottom 2s;
}
</style>





